.arrowDown,
.arrowUp {
    height: 80px;
    margin-top: -20px;
    position: relative;
    box-sizing: border-box;
    cursor: pointer;
    transform: scaleY(.5);
}

.arrowDown::before,
.arrowUp::before {
    content: '';
    position: absolute;
    left: calc(50% - 1vw);
    width: 3.5vw;
    height: 3.5vw;
    transform: rotate(-45deg);
    opacity: .75;
}

.arrowDown::before {
    border-left: 8px solid var(--light-blue);
    border-bottom: 8px solid var(--light-blue);
    animation: down 5s infinite;
}

.arrowDown:hover::before,
.arrowUp:hover::before {
    opacity: 1;
}

.arrowUp::before {
    top: 30px;
    border-top: 8px solid var(--dark-blue);
    border-right: 8px solid var(--dark-blue);
}

@keyframes down {
    30% {
        top: 0;
    }
    38% {
        top: 25px;
    }
    43% {
        top: 10px;
    }
    48% {
        top: 25px;
    }
    60% {
        top: 0;
    }

}