.footerWrap {
    width: 100%;
    min-height: 10px;
    padding: 15px;
    background-color: var(--deep-blue);
    border-top: 1px solid var(--light-blue);
    position: absolute;
    bottom: 0;
    left: 0;
    text-align: center;
    opacity: 0;
    transition: .5s ease all;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
}

.footerVisible {
    opacity: 1;
}

.mediaschool {
    height: 60px;
}

.nornikel,
.gorod {
    height: 30px;
}

.footerWrap img {
    height: 100%;
}

.footerAddresses {
    font-family: var(--sans);
    font-size: .9rem;
    text-align: left;
    color: white;
    border-left: 1px solid var(--light-blue);
    padding-left: 30px;
}
